.promiseSec {
  box-sizing: border-box;
  width: 100%;
  height: 84.5vh;
  background-color: white;
  border-top: 2px solid lightgray;
  padding: 0.8em;
}
.titleSec {
  border-bottom: 1px solid lightgray;
  height: 10%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.contentSec {
  height: 80%;
}
.title {
  font-size: 1.2rem;
  font-weight: 550;
}
.btnSec {
  width: 100%;
  height: 10%;
  display: flex;
  justify-content: space-between;
}
.nextBtn {
  height: 100%;
  width: 100%;
  background-color: #fbba00;
  color: white;
  border-radius: 10px;
}
.beforeBtn {
  height: 100%;
  width: 48%;
  background-color: #3c4451;
  color: white;
  border-radius: 10px;
}
.promiseBtn {
  height: 100%;
  width: 48%;
  background-color: #fbba00;
  color: white;
  border-radius: 10px;
}
h2 {
  margin: 0;
}
